import React from "react";
import styled from 'styled-components';
import { useStaticQuery, Link, graphql } from "gatsby";
import GitHubLogo from '../../static/GitHub.png';
import LinkedInLogo from '../../static/LinkedIn.png'

const LogoBox = styled.li`
  display: flex;
  float: right;
`;

const LogoSize = styled.img`
  width: 30px;
  height: 30px;
  margin: 5px;
`;

const LogoLink = styled.a`
  background-image: none;
`;

const ListLink = props => (
  <li style={{ display: `inline-block`, marginRight: `1rem` }}>
    <Link to={props.to}>{props.children}</Link>
  </li>
)


export default ({ children }) => {
  const data = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  )
  
  return (
    <div style={{ margin: `3rem auto`, maxWidth: 850, padding: `0 1rem`, textShadow: `none`, }}>
      <header style={{ marginBottom: `1.5rem`, textShadow: `none` }}>
        <Link to="/" style={{ textShadow: `none`, backgroundImage: `none` }}>
          <h3 style={{ display: `inline` }}>{data.site.siteMetadata.title}</h3>
        </Link>
        <ul style={{ listStyle: `none`, float: `right` }}>
          <ListLink to="/">Home</ListLink>
          <ListLink to="/about/">About</ListLink>
          <ListLink to="/contact/">Contact</ListLink>
          <ListLink to="/resume/">Resume</ListLink>
          <LogoBox>
            <LogoLink href="https://www.linkedin.com/in/simonc989/">
              <LogoSize src={LinkedInLogo} alt="LinkedIn Logo" />
            </LogoLink>
            <LogoLink href="https://github.com/SimonC989">
              <LogoSize src={GitHubLogo} alt="GitHub Logo" />
            </LogoLink>
          </LogoBox>
        </ul>
      </header>
      {children}
    </div>
  )
}
